body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.blurred-city {
    width: 100%;
    height: 100vh;
}

.weather-container {
    border-radius: 15px;
    width: 90vw;
    position: absolute;
    top: 100px;
    right: 20px;
}

.input_custom{
    width: 20%;
    position: absolute;
    top: 100px;
    right: 68px;
   
    -webkit-filter: blur(0px);
   
            filter: blur(0px);

}

.ui.input>input{
    color:white;
}

.search_custom{
    font-size: 50px;
}

.location{
    display: grid;
    justify-content: space-around;

    grid-template-columns: 1fr auto 1fr;
    grid-gap: 10px;
    width: 70%;
    height: 78vh;
}

.temp_display{
    grid-column: 2 / 3;
    font-size: 110px;
    color: aliceblue;
    align-self: center;
    justify-self: center;
}

.temp_info{
    margin-left: 82px;
}

.header_display{
    font-size: 35px;
}

.header_display>p{
    font-size: 24px;
    padding: 20px;
}
